@import url(https://fonts.googleapis.com/css2?family=KoHo:wght@300&display=swap);
body{margin:0;font-family:"KoHo", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#212121;color:#f0f0f0;box-sizing:border-box}

.container{width:100vw;height:100vh;padding:32px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;box-sizing:border-box}

.button{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.button{-webkit-user-select:none;-ms-user-select:none;user-select:none}.button{font-size:20px;cursor:pointer;width:160px;height:48px;border-radius:4px}.button-normal{background-color:#ab47bc;color:#f0f0f0;transition:background-color 250ms ease-in-out;border:2px solid transparent}.button-normal:hover{background-color:#d500f9}.button-outlined{border:2px solid #ab47bc;color:#ab47bc;transition:all 250ms ease-in-out}.button-outlined:hover{border-color:#d500f9;color:#d500f9}

.actions{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin:0 64px}.actions>*{margin:0 16px}

.digit-picker{box-shadow:0px 0px 5px 5px rgba(0,0,0,0.4)}.digit-picker{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:5px;background-color:#383838;border-radius:4px;color:#f0f0f0}.digit-picker-row{display:-webkit-flex;display:flex}.digit-picker-btn,.digit-picker-clear-btn{border:1px solid #f0f0f0;border-radius:4px;cursor:pointer;font-size:16px;transition:all 250ms ease-in-out}.digit-picker-btn:hover,.digit-picker-clear-btn:hover{border-color:#d500f9;color:#d500f9}.digit-picker-btn{padding:4px;margin:4px;width:24px;height:24px}.digit-picker-clear-btn{margin:4px;padding:4px;height:24px}

.sudoku-digit{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.sudoku-digit{position:relative;width:32px;height:32px;border:1px solid transparent;background-color:#212121;cursor:pointer;font:inherit;font-size:24px;transition:border-color 250ms ease-in-out;color:#f0f0f0}@media (min-width: 768px){.sudoku-digit{width:64px;height:64px}}.sudoku-digit:hover{border-color:#ab47bc}.sudoku-digit-picker{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);z-index:1;cursor:auto;cursor:initial}.top-middle,.bottom-middle{border-left:1px solid #383838;border-right:1px solid #383838}.center-left,.center-right{border-top:1px solid #383838;border-bottom:1px solid #383838}.center-middle{border:1px solid #383838}

.sudoku-square{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;border:1px solid #f0f0f0;border-radius:4px}.sudoku-square-row{display:-webkit-flex;display:flex}

.sudoku{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.sudoku-row{display:-webkit-flex;display:flex}

